.cafe__menu {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.cafe__menu-content {
  flex: 1;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;

  margin-right: 4rem;
}

.cafe__menu-content_heading h1 {
  font-family: var(--font-family);
  font-size: 42px;
  line-height: 50px;

  margin-bottom: 4rem;
}

.cafe__menu-content_heading span {
  -webkit-text-fill-color: var(--color-btn) !important;
}

.cafe__menu-content_items {
  width: 100%;
}

.cafe__menu-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.cafe__menu-image img {
  width: 100%;
}

@media screen and (max-width: 1050px) {
  .cafe__menu {
    flex-direction: column-reverse;
  }

  .cafe__menu-image {
    margin-top: 0;
    margin-bottom: 4rem;
  }

  .cafe__menu-content {
    margin-right: 0;
  }
}

@media screen and (max-width: 950px) {
  .cafe__menu {
    padding: 1rem;
    margin-left: 2rem;
    margin-right: 2rem;
  }

  .cafe__menu-content_heading h1 {
    font-family: var(--font-family);
    font-size: 36px;
    line-height: 48px;
  }

  .cafe__menu-image {
    padding: 0;
  }
}

@media screen and (max-width: 480px) {
  .cafe__menu {
    padding: 2rem 1rem;
    margin-left: 0;
    margin-right: 0;
  }

  .cafe__menu-content_heading h1 {
    font-size: 30px;
    line-height: 38px;
    margin-bottom: 2rem;
  }
}