.cafe__contact {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  height: 100vh;
}

.cafe__contact-info {
  width: 80%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;

  margin-top: 10rem;
}

.cafe__contact-info_location {
  margin-right: 2rem;
}

.cafe__contact-info_location,
.cafe__contact-info_hours {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.cafe__contact-info_location h2,
.cafe__contact-info_hours h2 {
  font-family: var(--font-family);
  font-size: 28px;
  line-height: 36px;
  font-weight: 500;
  margin-bottom: 1rem;
}

.cafe__contact-info_location-container {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.cafe__contact-info_location-container p {
  font-family: var(--font-family);
  font-size: 18px;
  line-height: 24px;

  margin-left: 0.5rem;
}

.location_icon {
  color: #000;
}

.cafe__contact-info_hours-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.cafe__contact-info_hours-container div {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.cafe__contact-info_hours-container div p {
  font-family: var(--font-family);
  font-size: 18px;
  line-height: 24px;
}

.cafe__contact-info_hours-container div p:first-child {
  margin-right: 1.5rem;
}

@media screen and (max-width: 1000px) {
  .cafe__contact {
    padding: 4rem 1rem;
  }

  .cafe__contact-cta {
    padding-right: 4rem;
    padding-left: 4rem;
  }
}

@media screen and (max-width: 850px) {
  .cafe__contact-info {
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 0;
    padding-left: 4rem;
    padding-bottom: 4rem;
  }

  .cafe__contact-info_location {
    margin-bottom: 3rem;
  }

  .cafe__contact-info {
    padding: 0;
  }
}

@media screen and (max-width: 650px) {
  .cafe__contact {
    padding: 4rem 2rem;
  }

  .cafe__contact-cta {
    padding-right: 0;
    padding-left: 0;
  }
}

@media screen and (max-width: 480px) {
  .cafe__contact {
    padding: 0;
  }

  .cafe__contact-info_location {
    margin-bottom: 1.5rem;
  }

  .cafe__contact-info_location h2,
  .cafe__contact-info_hours h2 {
    font-size: 20px;
    margin-bottom: 0.4rem;
  }

  .cafe__contact-info_location-container p,
  .cafe__contact-info_hours-container div p {
    font-size: 14px;
    line-height: 20px;
  }
}

@media (orientation: landscape) {
  .cafe__contact {
    height: fit-content;
  }
}